<template>
  <DxDataGrid
    ref="gridContainer"
    :data-source="dataSource"
    :allow-column-reordering="true"
    :show-borders="true"
    key-expr="StudentID"
    @row-removed="remove"
    :hover-state-enabled="true"
    @row-updated="onRowUpdated"
  >
    <DxPaging :page-size="5" />
    <DxSearchPanel :visible="true" />
    <DxEditing :allow-deleting="true" :allow-updating="true" mode="cell" />
    <DxColumn data-field="StudentID" :allow-editing="false" />
    <DxColumn data-field="StudentName" />
    <DxColumn data-field="DateOfBirth" data-type="date" />
    <DxColumn
      data-field="Class"
      :width="300"
      cell-template="cellTemplate"
      edit-cell-template="tagBoxEditor"
    >
      <DxLookup
        :data-source="classes"
        value-expr="ClassID"
        display-expr="ClassName"
      />
    </DxColumn>
    <DxColumn data-field="Phone" />

    <template #cellTemplate="{ data: data }">
      <div>
        <div v-for="(i, index) in data.value" :key="index">
          {{ getClassNameById(i) }}
        </div>
      </div>
    </template>

    <template #tagBoxEditor="{ data: cellInfo }">
      <TagBoxComponent
        :value="cellInfo.value"
        :on-value-changed="(value) => onValueChanged(value, cellInfo)"
        :data-source="classes"
        :data-grid-component="cellInfo.component"
      />
    </template>
  </DxDataGrid>
</template>

<script>
import {
  DxDataGrid,
  DxColumn,
  DxEditing,
  DxPaging,
  DxSearchPanel,
  DxLookup,
} from "devextreme-vue/data-grid";
import {
  students,
  classes,
  getClassNameById,
  getIndex,
} from "../../../utils/educational-structure.js";
import TagBoxComponent from "../Form/TagBoxComponent.vue";
import notify from "devextreme/ui/notify";

export default {
  components: {
    DxDataGrid,
    DxColumn,
    DxEditing,
    DxPaging,
    DxSearchPanel,
    DxLookup,
    TagBoxComponent,
  },
  data() {
    return {
      dataSource: students,
      classes,
      students,
    };
  },
  methods: {
    getClassNameById,
    cellTemplate(container, options) {
      var noBreakSpace = "\u00A0",
        text = (options.value || [])
          .map((element) => {
            return options.column.lookup.calculateCellValue(element);
          })
          .join(", ");
      container.textContent = text || noBreakSpace;
      container.title = text;
    },
    remove({ key }) {
      notify("Successfully deleted", "success", 3000);
      students.splice(getIndex("Student", key), 1);
    },
    onValueChanged(newClass, { key, data }) {
      data.Class = newClass;
      students.splice(getIndex("Student", key), 1, data);
    },
    onRowUpdated({ key, data }) {
      students.splice(getIndex("Student", key), 1, data);
    },
  },
};
</script>
