<template>
  <div class="wrapper">
    <div class="title">Educational Structure</div>
    <div class="recordTable dx-card">
      <DxTabPanel
        :data-source="menu"
        :selected-item="menu[2]"
        :loop="false"
        :animation-enabled="true"
        :swipe-enabled="true"
      >
        <template #title="{ data: data }">
          <span>{{ data.Title }}</span>
        </template>
        <template #item="{ data: data }">
          <StudentMenuTable v-if="data.Title == 'Student Record'" />
          <InstructorMenuTable v-else-if="data.Title == 'Instructor Record'" />
          <ClassMenuTable v-else />
        </template>
      </DxTabPanel>
    </div>
    <div class="sub-wrapper">
      <div class="sub-wrapper-child dx-card">
        <DxDataGrid
          id="structureGrid"
          :allow-column-reordering="true"
          :data-source="rearrangedStudents"
          :show-borders="true"
          :allow-deleting="true"
          @row-removed="rowRemoved"
        >
          <DxColumn data-field="StudentName" />
          <DxColumn data-field="StudentID" />
          <DxColumn data-field="DateOfBirth" />
          <DxColumn :group-index="0" data-field="Class" />
          <DxEditing :allow-deleting="true" mode="cell" />
          <DxGroupPanel :visible="true" />
          <DxGrouping :auto-expand-all="false" />
          <DxSearchPanel :visible="true" />
          <DxScrolling mode="virtual" />
        </DxDataGrid>
      </div>
      <InputForm />
    </div>

    <DxPopup
      :visible.sync="popupVisible"
      :drag-enabled="false"
      :close-on-outside-click="true"
      :show-title="true"
      :width="500"
      :height="400"
      title="Information"
    >
      <ol type="1">
        <li>This is a fully functioning database management which consists of relation between classes, instructors and students.</li>
        <br />
        <li>Adding, Editing (click on record) or Deleting any of the record will automatically update the entire data relationship.</li>
        <br />
        <li>The top table shows the particular classes, instructors and students records in detail.</li>
        <br />
        <li>The bottom left table shows the educational structure grouped by classes.</li>
        <br />
        <li>The bottom right input form used to add any data - Auto Fill feature is provided.</li>
        <br />
      </ol>
    </DxPopup>
  </div>
</template>

<script>
import {
  StudentMenuTable,
  InstructorMenuTable,
  ClassMenuTable
} from "../components/EducationalStructure/MenuTable";
import InputForm from "../components/EducationalStructure/Form/InputForm";
import {
  students,
  instructors,
  classes,
  rearrangeStudents,
  getIndex
} from "../utils/educational-structure.js";
import DxTabPanel from "devextreme-vue/tab-panel";
import {
  DxDataGrid,
  DxColumn,
  DxGrouping,
  DxGroupPanel,
  DxSearchPanel,
  DxScrolling,
  DxEditing
} from "devextreme-vue/data-grid";
import { DxPopup } from "devextreme-vue/popup";

export default {
  components: {
    DxColumn,
    DxGroupPanel,
    DxGrouping,
    DxSearchPanel,
    DxDataGrid,
    DxEditing,
    DxScrolling,
    DxTabPanel,
    StudentMenuTable,
    InstructorMenuTable,
    ClassMenuTable,
    InputForm,
    DxPopup
  },
  data() {
    return {
      students,
      instructors,
      classes,
      rearrangedStudents: rearrangeStudents(),
      menu: [
        {
          Title: "Class Record",
          Data: classes
        },
        {
          Title: "Instructor Record",
          Data: instructors
        },
        {
          Title: "Student Record",
          Data: students
        }
      ],
      popupVisible: true
    };
  },
  watch: {
    students() {
      this.rearrangedStudents = rearrangeStudents();
    },
    instructors() {
      this.rearrangedStudents = rearrangeStudents();
    },
    classes() {
      this.rearrangedStudents = rearrangeStudents();
    }
  },
  methods: {
    rowRemoved({ data }) {
      let classID = data.Class.split("-")[0];
      let studentID = data.StudentID;

      let studentIndex = getIndex("Student", studentID);
      let studentRecord = students[studentIndex];

      let result = studentRecord.Class.filter(r => r !== classID);

      studentRecord.Class = result;
      students.splice(studentIndex, 1, studentRecord);
    }
  }
};
</script>

<style lang="scss" scoped>
#structureGrid {
  height: 425px;
}

.recordTable {
  margin-bottom: 20px;
}

.sub-wrapper {
  display: flex;
  justify-content: space-between;

  &-child {
    width: 49%;

    &:last-child {
      padding: 20px;
    }
  }
}
</style>
