<template>
  <div class="wrapper">
    <div class="title">
      Rate of Bacteria Growth
    </div>
    <DxChart :data-source="bacteria" palette="Harmony Light">
      <DxCommonSeriesSettings
        argument-field="time"
        :ignore-empty-points="true"
      />
      <DxArgumentAxis tick-interval="1" :visual-range="visualRange" />
      <DxScrollBar :visible="true" />
      <DxZoomAndPan argument-axis="both" />
      <DxSeries name="Bacteria A" value-field="bA" type="spline" color="red" />
      <DxSeries name="Bacteria B" value-field="bB" type="spline" color="blue" />
      <DxSeries
        name="Bacteria C"
        value-field="bC"
        type="spline"
        color="green"
      />
      <DxValueAxis :allow-decimals="false">
        <DxConstantLine
          :value="peakA"
          color="red"
          dash-style="dash"
          :width="2"
        />
        <DxConstantLine
          :value="peakB"
          color="blue"
          dash-style="dash"
          :width="2"
        />
        <DxConstantLine
          :value="peakC"
          color="green"
          dash-style="dash"
          :width="2"
        />
      </DxValueAxis>
    </DxChart>
    <div class="button-control">
      <DxButton
        @click="start"
        text="Start"
        type="success"
        width="200"
        height="50"
        :disabled="isRunning || isFinish"
      />
      <DxButton
        @click="stop"
        text="Stop"
        type="danger"
        width="200"
        height="50"
        :disabled="!isRunning"
      />
      <DxButton
        @click="reset"
        text="Reset"
        type="default"
        width="200"
        height="50"
        :disabled="isRunning || !alreadyStart"
      />
    </div>
    <div class="slider-control">
      <DxRangeSelector
        id="rangeSelector"
        :value.sync="rangeA"
        title="Bacteria A - Growth"
      >
        <DxScale :start-value="-10" :end-value="20" :tick-interval="3" />
      </DxRangeSelector>
      <DxRangeSelector
        id="rangeSelector"
        :value.sync="rangeB"
        title="Bacteria B - Growth"
      >
        <DxScale :start-value="-10" :end-value="20" :tick-interval="3" />
      </DxRangeSelector>
      <DxRangeSelector
        id="rangeSelector"
        :value.sync="rangeC"
        title="Bacteria C - Growth"
      >
        <DxScale :start-value="-10" :end-value="20" :tick-interval="3" />
      </DxRangeSelector>
    </div>
    <div class="sub-wrapper">
      <div>
        <div class="subtitle">Peak Bacteria A</div>
        <DxRadioGroup :items="peak" :value.sync="peakA" layout="horizontal" />
      </div>
      <div>
        <div class="subtitle">Peak Bacteria B</div>
        <DxRadioGroup :items="peak" :value.sync="peakB" layout="horizontal" />
      </div>
      <div>
        <div class="subtitle">Peak Bacteria C</div>
        <DxRadioGroup :items="peak" :value.sync="peakC" layout="horizontal" />
      </div>
    </div>
  </div>
</template>
<script>
import DxChart, {
  DxCommonSeriesSettings,
  DxArgumentAxis,
  DxSeries,
  DxConstantLine,
  DxValueAxis,
  DxScrollBar,
  DxZoomAndPan,
} from "devextreme-vue/chart";
import { DxButton, DxRadioGroup } from "devextreme-vue";
import { DxRangeSelector, DxScale } from "devextreme-vue/range-selector";
import { bacteria } from "../utils/growth-simulation";

export default {
  components: {
    DxChart,
    DxCommonSeriesSettings,
    DxArgumentAxis,
    DxConstantLine,
    DxValueAxis,
    DxSeries,
    DxButton,
    DxRangeSelector,
    DxScale,
    DxRadioGroup,
    DxScrollBar,
    DxZoomAndPan,
  },
  data() {
    return {
      bacteria,
      rangeA: [-10, 20],
      rangeB: [0, 20],
      rangeC: [10, 20],
      peakA: 100,
      peakB: 150,
      peakC: 200,
      returnA: false,
      returnB: false,
      returnC: false,
      peak: [0, 50, 100, 150, 200],
      isRunning: false,
      isFinish: false,
      alreadyStart: false,
      intervalHandler: "",
    };
  },
  computed: {
    visualRange() {
      let res = this.bacteria.length < 15 ? this.bacteria.length - 1 : 15;
      return { length: res };
    },
  },
  methods: {
    getNewValue(bacteria) {
      let range = 0;
      if (bacteria == "A") {
        range = this.rangeA;
      } else if (bacteria == "B") {
        range = this.rangeB;
      } else if (bacteria == "C") {
        range = this.rangeC;
      } else {
        return 0;
      }
      return Math.floor(Math.random() * (range[1] - range[0] + 1)) + range[0];
    },
    start() {
      // starting
      this.alreadyStart = true;
      this.isRunning = true;
      this.intervalHandler = setInterval(() => {
        let length = bacteria.length;

        let newBA = this.returnA
          ? bacteria[length - 1].bA - this.getNewValue("A")
          : bacteria[length - 1].bA + this.getNewValue("A");
        let newBB = this.returnB
          ? bacteria[length - 1].bB - this.getNewValue("B")
          : bacteria[length - 1].bB + this.getNewValue("B");
        let newBC = this.returnC
          ? bacteria[length - 1].bC - this.getNewValue("C")
          : bacteria[length - 1].bC + this.getNewValue("C");

        if (newBA > this.peakA) this.returnA = true;
        if (newBB > this.peakB) this.returnB = true;
        if (newBC > this.peakC) this.returnC = true;

        if (newBA <= 0) newBA = 0;
        if (newBB <= 0) newBB = 0;
        if (newBC <= 0) newBC = 0;

        if (!newBA && !newBB && !newBC) {
          this.isFinish = true;
          this.stop();
        }

        bacteria.push({
          time: length,
          bA: newBA,
          bB: newBB,
          bC: newBC,
        });
      }, 1500);
    },
    stop() {
      // stopping
      this.isRunning = false;
      clearInterval(this.intervalHandler);
    },
    reset() {
      // resetting
      this.alreadyStart = false;
      this.isFinish = false;
      this.bacteria.splice(1, this.bacteria.length - 1);
    },
  },
};
</script>

