import Vue from "vue";
import Router from "vue-router";

import auth from "./auth";

// import Home from "./views/home";
import EducationalStructure from "./views/educational-structure";
import PivotAnalysis from "./views/pivot-analysis";
import GrowthSimulation from "./views/growth-simulation";
import SalesComparison from "./views/sales-comparison";
import RoomBooking from "./views/room-booking";
import WebMobileApp from "./views/web-mobile-app";
import NativeMobileApp from "./views/native-mobile-app";
import RealTimeApp from "./views/real-time-app";
import Miscellaneous from "./views/miscellaneous";
import DisplayData from "./views/display-data";
import Contact from "./views/contact.vue";
import defaultLayout from "./layouts/side-nav-inner-toolbar";
import simpleLayout from "./layouts/single-card";

Vue.use(Router);

const router = new Router({
  routes: [
    // {
    //   path: "/home",
    //   name: "home",
    //   meta: { requiresAuth: true },
    //   components: {
    //     layout: defaultLayout,
    //     content: Home,
    //   },
    // },
    {
      path: "/educational-structure",
      name: "educational-structure",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: EducationalStructure,
      },
    },
    {
      path: "/pivot-analysis",
      name: "pivot-analysis",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: PivotAnalysis,
      },
    },
    {
      path: "/growth-simulation",
      name: "growth-simulation",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: GrowthSimulation,
      },
    },
    {
      path: "/home",
      name: "home",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: SalesComparison,
      },
    },
    {
      path: "/room-booking",
      name: "room-booking",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: RoomBooking,
      },
    },
    {
      path: "/web-mobile-app",
      name: "web-mobile-app",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: WebMobileApp,
      },
    },
    {
      path: "/native-mobile-app",
      name: "native-mobile-app",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: NativeMobileApp,
      },
    },
    {
      path: "/real-time-app",
      name: "real-time-app",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: RealTimeApp,
      },
    },
    {
      path: "/miscellaneous",
      name: "miscellaneous",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: Miscellaneous,
      },
    },
    {
      path: "/display-data",
      name: "display-data",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: DisplayData,
      },
    },
    {
      path: "/contact",
      name: "contact",
      meta: { requiresAuth: true },
      components: {
        layout: defaultLayout,
        content: Contact,
      },
    },
    {
      path: "/login-form",
      name: "login-form",
      meta: { requiresAuth: false },
      components: {
        layout: simpleLayout,
        // route level code-splitting
        // this generates a separate chunk (login.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        content: () =>
          import(/* webpackChunkName: "login" */ "./views/login-form"),
      },
    },
    {
      path: "/",
      redirect: "/home",
    },
    {
      path: "/recovery",
      redirect: "/home",
    },
    {
      path: "*",
      redirect: "/home",
    },
  ],
});

router.beforeEach((to, from, next) => {
  if (to.name === "login-form" && auth.authenticated()) {
    next({ name: "home" });
  }

  if (to.matched.some((record) => record.meta.requiresAuth)) {
    if (!auth.authenticated()) {
      next({
        name: "login-form",
        query: { redirect: to.fullPath },
      });
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
