<template>
  <div class="sub-wrapper-child dx-card">
    <form @submit="submitForm">
      <div class="form-title">
        <div>Input Form</div>
        <div>
          <DxButton
            :width="120"
            text="Auto Fill"
            type="default"
            styling-mode="outlined"
            @click="autoFill"
          />
          <DxButton
            :width="90"
            text="Save"
            type="default"
            styling-mode="contained"
            :use-submit-behavior="true"
          />
        </div>
      </div>
      <div class="dx-field">
        <div class="dx-field-label">Input Mode:</div>
        <div class="dx-field-value">
          <DxRadioGroup
            :items="inputMode"
            :value="inputMode[2]"
            layout="horizontal"
            @value-changed="modeChanged"
          />
        </div>
      </div>
      <div v-if="mode == 'Class' || mode == 'Instructor' || mode == 'Student'">
        <div class="dx-field">
          <div class="dx-field-label" v-if="mode == 'Instructor' || mode == 'Student'">Full Name:</div>
          <div class="dx-field-label" v-if="mode == 'Class'">Class Name:</div>
          <div class="dx-field-value">
            <DxTextBox :placeholder="textField" v-model="name">
              <DxValidator ref="inputFormName">
                <DxRequiredRule message="Name is required - just press auto fill" />
              </DxValidator>
            </DxTextBox>
          </div>
        </div>
        <div class="dx-field" v-if="mode == 'Instructor' || mode == 'Student'">
          <div class="dx-field-label">Date of Birth:</div>
          <div class="dx-field-value">
            <DxDateBox placeholder="mm/DD/yyyy" type="date" v-model="dob" />
          </div>
        </div>
        <div class="dx-field" v-if="mode == 'Instructor' || mode == 'Student'">
          <div class="dx-field-label">Phone Number:</div>
          <div class="dx-field-value">
            <DxTextBox mask="(852) 0000-0000" v-model="phone" mode="tel" />
          </div>
        </div>
        <div class="dx-field" v-if="mode == 'Student'">
          <div class="dx-field-label">Select Classes:</div>
          <div class="dx-field-value">
            <DxTagBox
              :dataSource="classes"
              display-expr="ClassName"
              value-expr="ClassID"
              v-model="selectClass"
              :show-selection-controls="true"
              :wrap-item-text="true"
              apply-value-mode="useButtons"
              height="50px"
            />
          </div>
        </div>
        <div class="dx-field" v-if="mode == 'Class'">
          <div class="dx-field-label">Instructor:</div>
          <div class="dx-field-value">
            <DxSelectBox
              :dataSource="instructors"
              display-expr="Instructor"
              value-expr="InstructorID"
              v-model="selectInstructor"
            />
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import {
  students,
  instructors,
  classes,
  generateRandomName,
  generateRandomClass,
  generateRandomDate,
  randomlySelectClass
} from "../../../utils/educational-structure";
import {
  DxRadioGroup,
  DxTextBox,
  DxDateBox,
  DxSelectBox,
  DxTagBox,
  DxButton
} from "devextreme-vue";

import { DxValidator, DxRequiredRule } from "devextreme-vue/validator";
import notify from "devextreme/ui/notify";

export default {
  components: {
    DxRadioGroup,
    DxTextBox,
    DxDateBox,
    DxSelectBox,
    DxTagBox,
    DxButton,
    DxValidator,
    DxRequiredRule
  },
  data() {
    return {
      inputMode: ["Class", "Instructor", "Student"],
      mode: "Student",
      name: "",
      dob: "",
      phone: "",
      selectClass: [],
      selectInstructor: "",

      students,
      instructors,
      classes
    };
  },
  computed: {
    textField() {
      let mode = this.mode;
      if (mode !== null) {
        if (mode == "Student" || mode == "Instructor") {
          return "Enter the full name...";
        } else {
          return "Enter the class name...";
        }
      }
      return "";
    }
  },
  methods: {
    randomlySelectClass,
    generateRandomDate,
    generateRandomClass,
    generateRandomName,
    reset() {
      this.name = "";
      this.dob = "";
      this.phone = "";
      this.selectClass = [];
      this.selectInstructor = "";
    },
    submitForm(e) {
      if (this.mode == "Student") {
        students.push({
          StudentName: this.name,
          StudentID: "S" + Math.ceil(Math.random() * 10000),
          DateOfBirth: this.dob,
          Class: this.selectClass,
          Phone: this.phone
        });
      } else if (this.mode == "Instructor") {
        instructors.push({
          InstructorID: "X" + Math.ceil(Math.random() * 10000),
          Instructor: this.name,
          DateOfBirth: this.dob,
          Phone: this.phone
        });
      } else if (this.mode == "Class") {
        classes.push({
          ClassID: "C" + Math.ceil(Math.random() * 10000),
          ClassName: this.name,
          Instructor: this.selectInstructor
        });
      }
      notify("Data has been added", "success", 3000);
      this.reset();
      this.$refs.inputFormName.instance.reset();
      e.preventDefault();
    },
    modeChanged({ value }) {
      this.reset();
      this.$refs.inputFormName.instance.reset();
      this.mode = value;
    },
    autoFill() {
      if (!this.name) {
        if (this.mode == "Student") this.name = this.generateRandomName();
        if (this.mode == "Instructor")
          this.name = "Dr. " + this.generateRandomName();
        if (this.mode == "Class") this.name = this.generateRandomClass();
      }

      if (!this.dob) {
        this.dob = this.generateRandomDate();
      }

      if (!this.phone) {
        this.phone = (
          Math.floor(Math.random() * 90000000) + 10000000
        ).toString();
      }

      if (this.mode == "Student" && !this.selectClass.length) {
        this.selectClass = this.randomlySelectClass();
      }

      if (this.mode == "Class" && !this.selectInstructor) {
        this.selectInstructor =
          instructors[
            Math.floor(Math.random() * instructors.length)
          ].InstructorID;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../../themes/generated/variables.base.scss";
.form-title {
  display: flex;
  justify-content: space-between;
  font-size: 25px;
  margin-top: 10px;
  margin-bottom: 10px;

  .dx-button {
    margin-left: 20px;
  }
}
</style>
