<template>
  <div class="wrapper">
    <div class="title">Sales Comparison</div>
    <div class="subtitle">
      <div></div>
      <div>Data on Compare</div>
    </div>
    <div class="widget-container">
      <DxList :items="data" :repaint-changes-only="true" key-expr="id">
        <template #item="{ data }"
          >{{ data.text }} ({{ tranformPrice(data.sales) }})</template
        >
        <DxItemDragging
          :data="data"
          :allow-reordering="true"
          :on-drag-start="onDragStart"
          :on-add="onAdd"
          :on-remove="onRemove"
          group="tasks"
        />
      </DxList>
      <DxList
        id="data-on-compare"
        :items="dataOnCompare"
        :repaint-changes-only="true"
        key-expr="id"
      >
        <template #item="{ data }"
          >{{ data.text }} ({{ tranformPrice(data.sales) }})</template
        >
        <DxItemDragging
          :data="dataOnCompare"
          :allow-reordering="true"
          :on-drag-start="onDragStart"
          :on-add="onAdd"
          :on-remove="onRemove"
          group="tasks"
        />
      </DxList>
    </div>
    <div class="widget-container overlap-prevention">
      <div>
        <DxPieChart
          id="pie"
          title="Total Sales - Doughnut Chart"
          :data-source="dataOnCompare"
          type="doughnut"
          palette="Soft Pastel"
        >
          <DxSeries argument-field="text" value-field="sales">
            <DxLabel
              :visible="true"
              format="thousands"
              :customize-text="customizeLabel"
            >
              <DxConnector :visible="true" />
            </DxLabel>
          </DxSeries>
          <DxLegend :visible="false" />
          <DxTooltip :enabled="true" :customize-tooltip="customizeTooltip">
            <DxFormat type="thousands" />
          </DxTooltip>
        </DxPieChart>
      </div>
      <div>
        <DxChart
          id="chart"
          :data-source="dataOnCompare"
          palette="Soft Pastel"
          :legend="{ visible: false }"
        >
          <DxTitle
            text="Total Sales - Bar Chart"
            horizontal-alignment="center"
          />
          <DxCommonSeriesSettings
            :ignore-empty-points="true"
            argument-field="text"
            value-field="sales"
            type="bar"
          />
          <DxSeriesTemplate name-field="text" />
        </DxChart>
      </div>
    </div>
  </div>
</template>
<script>
import DxList, { DxItemDragging } from "devextreme-vue/list";
import DxPieChart, {
  DxLegend,
  DxSeries,
  DxTooltip,
  DxFormat,
  DxLabel,
  DxConnector,
} from "devextreme-vue/pie-chart";
import {
  DxChart,
  DxSeriesTemplate,
  DxCommonSeriesSettings,
  DxTitle,
} from "devextreme-vue/chart";
import { dataOnCompare, data } from "../utils/sales-comparison";

export default {
  components: {
    DxList,
    DxItemDragging,
    DxPieChart,
    DxLegend,
    DxSeries,
    DxTooltip,
    DxFormat,
    DxLabel,
    DxConnector,
    DxChart,
    DxSeriesTemplate,
    DxCommonSeriesSettings,
    DxTitle,
  },
  data() {
    return {
      dataOnCompare,
      data,
    };
  },
  methods: {
    onDragStart(e) {
      e.itemData = e.fromData[e.fromIndex];
    },
    onAdd(e) {
      e.toData.splice(e.toIndex, 0, e.itemData);
    },
    onRemove(e) {
      e.fromData.splice(e.fromIndex, 1);
    },
    customizeTooltip({ valueText, percent }) {
      return {
        text: `${valueText} - ${(percent * 100).toFixed(2)}%`,
      };
    },
    customizeLabel({ argumentText, valueText }) {
      return `${argumentText}: ${valueText}`;
    },
    tranformPrice(amount) {
      let result = amount.toString();
      String.prototype.splice = function(idx, rem, str) {
        return this.slice(0, idx) + str + this.slice(idx + Math.abs(rem));
      };

      for (let i = result.length - 3; i > 0; i = i - 3) {
        result = result.splice(i, 0, ",");
      }

      return result;
    },
  },
};
</script>
