<template>
  <div class="tagBox">
    <DxTagBox
      :data-source="dataSource"
      :value.sync="currentValue"
      :show-selection-controls="true"
      :show-multi-tag-only="false"
      :on-value-changed="(e) => onValueChanged(e.value)"
      :on-selection-changed="onSelectionChanged"
      :search-enabled="true"
      value-expr="ClassID"
      display-expr="ClassName"
      apply-value-mode="useButtons"
    />
  </div>
</template>
<script>
import DxTagBox from "devextreme-vue/tag-box";

export default {
  components: { DxTagBox },
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    onValueChanged: {
      type: Function,
      default: () => function() {},
    },
    dataSource: {
      type: Array,
      default: () => {},
    },
    dataGridComponent: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      currentValue: this.value,
    };
  },
  methods: {
    onSelectionChanged() {
      this.dataGridComponent.updateDimensions();
    },
  },
};
</script>

<style lang="scss" scoped>
.tagBox {
  padding-top: 20px;
  //   padding-bottom: 20px;
}
</style>
