<template>
  <div class="showtime-preview">
    <div>{{ purposeData.text }}</div>
    <div>
      Booked by
      <strong>{{ templateModel.appointmentData.host }}</strong>
    </div>
  </div>
</template>
<script>
import Query from "devextreme/data/query";

import { purposeData } from "../../utils/room-booking";

const getMovieById = function(resourceId) {
  return Query(purposeData)
    .filter("id", resourceId)
    .toArray()[0];
};

export default {
  props: {
    scheduler: {
      type: Object,
      default: () => {},
    },
    templateModel: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      purposeData: getMovieById(this.templateModel.appointmentData.purpose),
    };
  },
};
</script>
