<template>
  <div class="wrapper">
    <div class="title">Real Time App</div>
    <div>
      <div class="subtitle">
        <div>-- -- -- Private Chat Room with Nuxt.js + Socket.io -- -- --</div>
        <div>
          <DxButton
            text="tools integration"
            type="normal"
            @click="showDialog = true"
          />
          <DxPopup
            :visible.sync="showDialog"
            :drag-enabled="false"
            :close-on-outside-click="true"
            :show-title="true"
            :width="410"
            :height="530"
            title="Tools Integration"
          >
            <center>
              <img src="../assets/componentNuxt.png" width="350" height="400" />
            </center>
          </DxPopup>
        </div>
      </div>
      <div class="body">
        <div>
          <DxList
            :data-source="dataSource"
            keyExpr="id"
            selection-mode="single"
            :width="350"
            :selected-item-keys.sync="selectedItemKeys"
          />
        </div>
        <center>
          <img :src="url" class="picture" @load="loaded" v-show="isLoaded" />
          <div class="margin">
            <DxLoadIndicator :width="65" :height="65" v-show="!isLoaded" />
          </div>
        </center>
      </div>
    </div>
  </div>
</template>

<script>
import DxList from "devextreme-vue/list";
import { DxPopup } from "devextreme-vue/popup";
import { DxButton } from "devextreme-vue";
import { DxLoadIndicator } from 'devextreme-vue/load-indicator';
export default {
  data() {
    return {
      isLoaded: false,
      showDialog: false,
      selectedItemKeys: [1],
      dataSource: [
        { id: 1, text: "Demonstration Part 1 - Signing In", url: "part1.gif" },
        {
          id: 2,
          text: "Demonstration Part 2 - Sending Messages",
          url: "part2.gif"
        },
        { id: 3, text: "Demonstration Part 3 - Signing Out", url: "part3.gif" }
      ]
    };
  },
  computed: {
    url() {
      return this.selectedItemKeys.length
        ? require("../assets/" +
            this.dataSource[this.selectedItemKeys[0] - 1].url)
        : "";
    }
  },
  watch: {
    selectedItemKeys() {
      this.isLoaded = false;
    }
  },
  methods: {
    loaded() {
      this.isLoaded = true;
    }
  },
  components: {
    DxButton,
    DxPopup,
    DxList,
    DxLoadIndicator
  }
};
</script>
