<template>
  <div class="wrapper">
    <div class="title">Flutter Mobile App</div>

    <center>
      <div class="body dx-card">
        <div class="relative">
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="https://github.com/kevinnhansenn/my-web-and-native-projects"
          >[GO TO GITHUB REPO]</a>
        </div>
        <div>
          <DxList
            :data-source="dataSource"
            keyExpr="id"
            selection-mode="single"
            :width="450"
            :selected-item-keys.sync="selectedItemKeys"
          />
        </div>
        <div class="center">
          <img id="gif" :src="url" @load="loaded" v-show="isLoaded"/>
          <DxLoadIndicator :width="65" :height="65" v-show="!isLoaded"/>
        </div>
      </div>
    </center>
  </div>
</template>

<script>
import DxList from "devextreme-vue/list";
import { DxLoadIndicator } from 'devextreme-vue/load-indicator';
import { projects, getUrlById } from "../utils/native-mobile-app";
export default {
  components: {
    DxList,
    DxLoadIndicator
  },
  data() {
    return {
      dataSource: projects,
      selectedItemKeys: [1],
      isLoaded: false
    };
  },
  computed: {
    url() {
      return this.selectedItemKeys[0]
        ? require("../assets/" + getUrlById(this.selectedItemKeys[0]))
        : "";
    }
  },
  watch: {
    selectedItemKeys() {
      this.isLoaded = false;
    }
  },
  methods: {
    loaded() {
      this.isLoaded = true;
    }
  }
};
</script>

