<template>
  <DxDataGrid
    id="gridContainer"
    :data-source="dataSource"
    :allow-column-reordering="true"
    :show-borders="true"
    key-expr="InstructorID"
    @row-removing="remove"
    @row-updated="onRowUpdated"
  >
    <DxPaging :page-size="5" />
    <DxSearchPanel :visible="true" />
    <DxEditing :allow-deleting="true" :allow-updating="true" mode="cell" />
    <DxColumn data-field="InstructorID" :allow-editing="false" />
    <DxColumn data-field="Instructor" />
    <DxColumn data-field="DateOfBirth" data-type="date" />
    <DxColumn data-field="Phone" />
  </DxDataGrid>
</template>

<script>
import {
  DxDataGrid,
  DxColumn,
  DxEditing,
  DxPaging,
  DxSearchPanel,
} from "devextreme-vue/data-grid";
import {
  instructors,
  classes,
  getIndex,
} from "../../../utils/educational-structure.js";
import notify from "devextreme/ui/notify";

export default {
  components: {
    DxDataGrid,
    DxColumn,
    DxEditing,
    DxPaging,
    DxSearchPanel,
  },
  data() {
    return {
      classes,
      instructors,
      dataSource: instructors,
    };
  },
  methods: {
    remove(e) {
      let canRemove = true;

      classes.forEach((c) => {
        if (c.Instructor == e.key) {
          canRemove = false;
        }
      });

      if (canRemove) {
        for (let i = 0; i < instructors.length; i++) {
          if (instructors[i].StudentID == e.key) {
            instructors.splice(i, 1);
            break;
          }
        }
        notify("Successfully deleted", "success", 3000);
      } else {
        e.cancel = true;
        notify(
          "The instructor has assigned class, try to remove the class first",
          "error",
          3000
        );
      }
    },
    onRowUpdated({ key, data }) {
      instructors.splice(getIndex("Instructor", key), 1, data);
    },
  },
};
</script>
