export const rearrangeStudents = function() {
  let result = [];
  for (let student of students) {
    for (let i = 0; i < student.Class.length; i++) {
      let dummy = { ...student };
      let individualClass =
        student.Class[i] +
        "-" +
        getClassNameById(student.Class[i]) +
        " by " +
        getInstructorNameByClassId(student.Class[i]);
      dummy.Class = individualClass;
      result.push(dummy);
    }
  }
  return result;
};

export const removeClassFromStudent = function() {
  getIndex();
};

export const getClassNameById = function(classID) {
  let result = "";
  classes.forEach((c) => {
    if (c.ClassID == classID) return (result = c.ClassName);
  });
  return result;
};

export const getInstructorNameById = function(instructorID) {
  let result = "";
  instructors.forEach((i) => {
    if (i.InstructorID == instructorID) return (result = i.Instructor);
  });
  return result;
};

export const getInstructorNameByClassId = function(classID) {
  let instructor = "";
  let result = "";
  classes.forEach((c) => {
    if (c.ClassID == classID) return (instructor = c.Instructor);
  });
  instructors.forEach((i) => {
    if (i.InstructorID == instructor) return (result = i.Instructor);
  });
  return result;
};

export const getIndex = function(data, key) {
  if (data == "Student") {
    for (let i = 0; i < students.length; i++) {
      if (students[i].StudentID == key) {
        return i;
      }
    }
  } else if (data == "instructor") {
    for (let i = 0; i < instructors.length; i++) {
      if (instructors[i].InstructorID == key) {
        return i;
      }
    }
  } else {
    for (let i = 0; i < classes.length; i++) {
      if (classes[i].ClassID == key) {
        return i;
      }
    }
  }
};

