export const generateRandomName = function() {
  return (
    name[Math.floor(Math.random() * name.length)] +
    " " +
    name[Math.floor(Math.random() * name.length)]
  );
};

export const generateRandomClass = function() {
  return (
    adjective[Math.floor(Math.random() * adjective.length)] +
    " " +
    subject[Math.floor(Math.random() * subject.length)]
  );
};

export const generateRandomDate = function() {
  return (
    Math.ceil(Math.random() * 12) +
    "/" +
    Math.ceil(Math.random() * 28) +
    "/" +
    (Math.ceil(Math.random() * 100) + 1970)
  );
};

export const randomlySelectClass = function() {
  let result = [];
  classes.forEach((c) => {
    Math.random() > 0.5 ? result.push(c.ClassID) : null;
  });
  return result;
};
