<template>
  <DxDataGrid
    id="gridContainer"
    :data-source="dataSource"
    :allow-column-reordering="true"
    :show-borders="true"
    key-expr="ClassID"
    @row-removing="remove"
    @row-updated="onRowUpdated"
  >
    <DxPaging :page-size="5" />
    <DxSearchPanel :visible="true" />
    <DxEditing :allow-deleting="true" :allow-updating="true" mode="cell" />
    <DxColumn data-field="ClassID" :allow-editing="false" />
    <DxColumn data-field="ClassName" />
    <DxColumn data-field="Instructor">
      <DxLookup
        :data-source="instructors"
        value-expr="InstructorID"
        display-expr="Instructor"
      />
    </DxColumn>
  </DxDataGrid>
</template>

<script>
import {
  DxDataGrid,
  DxColumn,
  DxEditing,
  DxPaging,
  DxSearchPanel,
  DxLookup,
} from "devextreme-vue/data-grid";
import {
  classes,
  instructors,
  students,
  getInstructorNameById,
  getIndex,
} from "../../../utils/educational-structure.js";
import notify from "devextreme/ui/notify";

export default {
  components: {
    DxDataGrid,
    DxColumn,
    DxEditing,
    DxPaging,
    DxSearchPanel,
    DxLookup,
  },
  data() {
    return {
      students,
      instructors,
      classes,
      dataSource: classes,
    };
  },
  methods: {
    getInstructorNameById,
    remove(e) {
      let canRemove = true;

      students.forEach((s) => {
        if (s.Class.indexOf(e.key) > -1) {
          canRemove = false;
        }
      });

      if (canRemove) {
        for (let i = 0; i < classes.length; i++) {
          if (classes[i].ClassID == e.key) {
            classes.splice(i, 1);
            break;
          }
        }
        notify("Successfully deleted", "success", 3000);
      } else {
        e.cancel = true;
        notify(
          "The class has assigned student, try to remove the student first",
          "error",
          3000
        );
      }
    },
    onRowUpdated({ key, data }) {
      classes.splice(getIndex("Class", key), 1, data);
    },
  },
};
</script>
