<template>
  <div class="wrapper">
    <div class="title">Room Booking System</div>
    <DxScheduler
      :data-source="bookingData"
      :views="views"
      :current-date="currentDate"
      :groups="groups"
      :first-day-of-week="0"
      :start-day-hour="9"
      :end-day-hour="18"
      :show-all-day-panel="false"
      :cross-scrolling-enabled="true"
      :editing="editing"
      :on-content-ready="onContentReady"
      :on-appointment-form-opening="onAppointmentFormOpening"
      appointment-template="AppointmentTemplateSlot"
      current-view="day"
    >
      <DxResource
        :use-color-as-default="true"
        :data-source="purposeData"
        field-expr="purpose"
      />
      <DxResource :data-source="roomData" field-expr="roomID" />
      <template #AppointmentTemplateSlot="{ data }">
        <AppointmentTemplate :scheduler="scheduler" :template-model="data" />
      </template>
    </DxScheduler>
  </div>
</template>

<script>
import DxScheduler, { DxResource } from "devextreme-vue/scheduler";
import AppointmentTemplate from "../components/RoomBooking/AppointmentTemplate.vue";

import { bookingData, purposeData, roomData } from "../utils/room-booking.js";
import Query from "devextreme/data/query";

const getPurposeById = function(resourceId) {
  return Query(purposeData)
    .filter("id", resourceId)
    .toArray()[0];
};

export default {
  components: {
    DxScheduler,
    DxResource,
    AppointmentTemplate,
  },
  data() {
    return {
      views: ["day", "week", "timelineDay"],
      groups: ["roomID"],
      scheduler: null,
      currentDate: new Date(2015, 4, 24),
      bookingData: bookingData,
      purposeData: purposeData,
      roomData: roomData,
      editing: { allowAdding: true },
    };
  },
  methods: {
    onContentReady(e) {
      this.scheduler = e.component;
    },
    onAppointmentFormOpening(data) {
      let form = data.form,
        purposeInfo = getPurposeById(data.appointmentData.purpose) || {},
        startDate = data.appointmentData.startDate;

      form.option("items", [
        {
          label: {
            text: "Purpose",
          },
          editorType: "dxSelectBox",
          dataField: "purpose",
          editorOptions: {
            items: purposeData,
            displayExpr: "text",
            valueExpr: "id",
            onValueChanged: function(args) {
              let purpose = getPurposeById(args.value);
              purposeInfo = { ...purpose };
              form
                .getEditor("endDate")
                .option(
                  "value",
                  new Date(startDate.getTime() + 60 * 1000 * purpose.duration)
                );
            },
          },
        },
        {
          label: {
            text: "Host",
          },
          name: "host",
          dataField: "host",
          editorType: "dxTextBox",
        },
        {
          dataField: "startDate",
          editorType: "dxDateBox",
          editorOptions: {
            width: "100%",
            type: "datetime",
            onValueChanged: function(args) {
              startDate = args.value;
              form
                .getEditor("endDate")
                .option(
                  "value",
                  new Date(
                    startDate.getTime() + 60 * 1000 * purposeInfo.duration
                  )
                );
            },
          },
        },
        {
          name: "endDate",
          dataField: "endDate",
          editorType: "dxDateBox",
          editorOptions: {
            width: "100%",
            type: "datetime",
            readOnly: true,
          },
        },
      ]);
    },
  },
};
</script>
