<template>
  <div class="wrapper">
    <div class="title">Pivot Analysis</div>
    <div align="center">
      <DxChart ref="chart" class="chart">
        <DxTooltip :enabled="true" :customize-tooltip="customizeTooltip" />
        <DxAdaptiveLayout :width="450" />
        <DxSize :height="300" />

        <DxCommonSeriesSettings type="bar" />
      </DxChart>
    </div>

    <div class="recordTable">
      <!-- <DxPivotGridFieldChooser
        :data-source="dataSource"
        :layout.sync="layout"
        :apply-changes-mode.sync="applyChangesMode"
        :state.sync="state"
        :width="300"
        :height="400"
      >
        <DxTexts
          all-fields="All"
          column-fields="Columns"
          data-fields="Data"
          row-fields="Rows"
          filter-fields="Filter"
        />
      </DxPivotGridFieldChooser>-->
      <DxPivotGrid
        id="pivotgrid"
        ref="grid"
        :allow-sorting-by-summary="true"
        :allow-sorting="true"
        :allow-filtering="true"
        :show-borders="true"
        :data-source="dataSource"
      >
        <DxFieldChooser :enabled="true" />
      </DxPivotGrid>
    </div>
  </div>
</template>
<script>
import { DxPivotGrid, DxFieldChooser } from "devextreme-vue/pivot-grid";

import {
  DxChart,
  DxAdaptiveLayout,
  DxCommonSeriesSettings,
  DxSize,
  DxTooltip,
} from "devextreme-vue/chart";

import PivotGridDataSource from "devextreme/ui/pivot_grid/data_source";

import service from "../utils/pivot-analysis.js";

export default {
  components: {
    DxPivotGrid,
    DxFieldChooser,
    DxChart,
    DxAdaptiveLayout,
    DxCommonSeriesSettings,
    DxSize,
    DxTooltip,
  },
  data() {
    let dataSource = new PivotGridDataSource({
      fields: [
        {
          caption: "Hardware",
          width: 120,
          dataField: "item",
          area: "row",
          headerFilter: {
            allowSearch: true,
          },
        },
        {
          caption: "Model",
          dataField: "model",
          width: 150,
          area: "row",
          headerFilter: {
            allowSearch: true,
          },
          selector: function(data) {
            return `${data.model} (${data.brand})`;
          },
        },
        {
          dataField: "date",
          dataType: "date",
          area: "column",
        },
        {
          caption: "Sales",
          dataField: "amount",
          dataType: "number",
          summaryType: "sum",
          format: "currency",
          area: "data",
        },
      ],
      store: service.getSales(),
    });
    return {
      dataSource: dataSource,
      state: dataSource.state(),
      layout: 0,
      applyChangesMode: "instantly",
    };
  },
  methods: {
    tranformPrice(amount) {
      let result = amount.toString();
      String.prototype.splice = function(idx, rem, str) {
        return this.slice(0, idx) + str + this.slice(idx + Math.abs(rem));
      };

      for (let i = result.length - 3; i > 0; i = i - 3) {
        result = result.splice(i, 0, ",");
      }

      return result;
    },
    customizeTooltip(args) {
      return {
        html: `${args.seriesName} - $${this.tranformPrice(
          args.originalValue
        )}</div>`,
      };
    },
  },
  mounted() {
    const pivotGrid = this.$refs.grid.instance;
    const chart = this.$refs.chart.instance;
    pivotGrid.bindChart(chart, {
      dataFieldsDisplayMode: "splitPanes",
      alternateDataFields: false,
    });
  },
};
</script>

